//  css 

@import "./mixin";

//  css 
:root {
  @each $style in map-keys($theme-property-values) {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877
    #{--theme-#{$style}}: map-get($theme-property-values, $style);
  }

  // 
  @each $theme, $color in $theme-assist-colors {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877
    #{--theme-#{$theme}}: $color;
  }

  // 
  @each $theme, $color in $theme-assist-colors-light {
    // FIXME  sass-lint  css variables #{} 
    // https://github.com/sasstools/sass-lint/issues/877
    #{--theme-#{$theme}-light}: $color;
  }
}

// 
// .theme-background {background-color: #fff; background-color: var(--theme-background, #fff);}
.theme-background {
  @include theme-prop(background-color, background);
}

//  primary  accent 
// .theme-primary {background-color: #2196f3; background-color: var(--theme-primary, #2196f3);}
@each $style in ("primary", "accent") {
  .theme-#{$style}-bg {
    @include theme-prop(background-color, $style, true);
  }
}

// 
// .theme-success-bg {background-color: #4caf50; background-color: var(--theme-success, #4caf50);}
@each $theme, $color in $theme-assist-colors {
  .theme-#{$theme}-bg {
    background-color: $color;
    background-color: var(--theme-#{$theme}, $color);
  }
}

// 
// .theme-text-primary-on-primary {color: #2196f3; color: var(--theme-text-primary-on-primary, #2196f3);}
@each $style in map-keys($theme-property-values) {
  @if $style != "background" {
    .theme-#{$style} {
      @include theme-prop(color, $style, true);
    }
  }
}

// 
// .theme-text-success {color: #4caf50; color: var(--theme-success, #4caf50);}
@each $theme, $color in $theme-assist-colors {
  .theme-text-#{$theme} {
    color: $color;
    color: var(--theme-#{$theme}, $color);
  }
}

